* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
html {
  font-size: 16px;
}

::-webkit-scrollbar {
  width: 9px;
}

::-webkit-scrollbar-thumb {
  background-color: mediumslateblue;
  border-radius: 20px;
}

::-webkit-scrollbar-track {
  background-color: #fff;
}

.media-heading-sec {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.media-heading-sec h1 {
  font-size: 1.7rem;
  font-weight: 500;
  text-align: center;
}
